header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.me__image {
  display: block;
  width: 94%;
  object-fit: cover;
  margin: auto;
}
.container_header {
  text-align: center;
  position: relative;
}

/* ======================== CTA HEADER ###############*/

.cta {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1.2rem;
}

/* ============================== HEADER SOCIALS ###########*/

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: -30rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ====================== ME ============= */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  transform: translateX(105%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ########################## SCROLL DOWN ############*/

.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: -28rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*############# MEDIA QUERIES (MEDIUM DEVICES) ##########*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/*############# MEDIA QUERIES (SMALL DEVICES) ##########*/

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header_socials,
  .scroll_down {
    display: none;
  }
}
